* {
  margin: 0;
}

a {
  -webkit-text-decoration: none;
  text-decoration: none;
  color: inherit;
}

body {
  background-color: #3b3a3a;
  font-family: "GothamPro-Light";
  font-size: 14px;
  font-weight: 600;
  letter-spacing: 0.012em;
  line-height: 1.5;
  color: #cccccc;
}
body .page {
  max-width: 1320px;
  margin: 0 auto;
  padding: 30px;
}
