  font-family: "GothamPro-Bold";
  border-radius: 20px;
  padding: 10px 15px;
  text-transform: uppercase;
  box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.14);
  background-image: -webkit-linear-gradient(top, #626262 0%, #626262 100%);
