  text-align: center;
  color: #efefef;
}
body .btn:hover {
  background-color: #5d5c5c;
  background-image: -webkit-linear-gradient(top, #5d5c5c 0%, #818181 100%);
  transition: all 0.3s ease-in-out;
}
